* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {

  font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
    width: 100%;
    margin: 0 auto;
    background: lightgrey;
    padding-bottom: 1rem;
}

@media (min-width:600px){
    .container {
        width: 50%;
        border-radius: 0.2rem;
    }
}


/* Header */
.todo-title {
    text-align: center;
    margin-top: 3rem;
    margin-bottom: 1rem;
}

.todo-title h1 {
    padding-top: 1rem;
}

/* Add Form */
.form-todo {
 text-align: center;
}

.form-todo input[type="text"] {
    border: 2px solid #aaa;
    border-radius:4px;
    box-sizing: border-box;
    height: 2.5rem;
    text-align: center;
    width: 68%;
    margin-right: 0.2rem;
}

.form-todo button {
    border: 2px solid #aaa;
    border-radius:4px;
    width: 3rem;
    height: 2.5rem;
    margin-right: 0.2rem;
}

.form-todo input[type="text"]:focus{
    border-color: dodgerblue;
    box-shadow: 0 0 8px 0 dodgerblue;
}



/* Todo List */
.unorderedTodos {
    list-style: none;
}

.unorderedTodos button {
    font-size: 1rem;
}

.completed {
    text-decoration: line-through;
}

.unorderedTodos input[type="text"] {
    border: 2px solid #aaa;
    border-radius:4px;
    box-sizing: border-box;
    width: 95%;
    height: 2.5rem;
    text-align: center;
}


.unorderedTodos button {
    border: 2px solid #aaa;
    border-radius:4px;
    width: 3rem;
    height: 3rem;
    margin: 0.5rem 0.7rem 0.5rem 0.7rem
}


@media (min-width:600px){

    .unorderedTodos input[type="text"] {
        width: 50%;
    }
    
    
    .unorderedTodos button {

        height: 2.3rem;
        margin: 0.4rem
    }
}

